.footer {
    padding-top: 80px;

    background-color: $secondary-background-color;
}

.footer-contacts-link__list {
    display: flex;
    justify-content: flex-end;
}

.footer-contacts-link__link {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;

    width: 34px;
    height: 34px;

    background-color: #fea5bb;

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        background-color: $accent-color;

        transform: scale(1.1);

        .footer-contacts-link__icon {
            fill: $priority-background-color;
        }
    }
}

.footer-contacts-link__icon {
    fill: currentColor;
}

.footer-contacts__text {
    font-weight: 700;
    line-height: 186%;
    text-align: right;

    color: #000000;
}

.footer-contacts__tel {
    color: $accent-color;
}

.copyright {
    border-top: 1px solid #e1e1e1;
}

.copyright__text {
    text-align: center;

    font-weight: 700;
    font-size: 14px;
    line-height: 152%;

    color: #907e82;
}
@media screen and (max-width: 767px) {
    .footer {
        padding-bottom: 25px;
    }

    .footer__text {
        margin-bottom: 42px;

        max-width: 230px;
    }

    .footer-text__suptitle {
        margin-bottom: 18px;
    }

    .footer-contacts-link__item {
        &:not(:last-child) {
            margin-right: 30px;
        }
    }

    .footer-contacts-link__list {
        margin-bottom: 46px;
    }

    .footer-contacts__text {
        font-size: 12px;
    }

    .footer__contacts-link {
        margin-bottom: 44px;
    }

    .copyright {
        padding-top: 22px;
    }

    .copyright__text {
        margin-left: auto;
        margin-right: auto;

        max-width: 150px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
    .footer {
        padding-bottom: 18px;
    }

    .footer {
        .container {
            margin-bottom: 90px;

            display: flex;
            justify-content: space-between;
        }
    }

    .footer__text {
        max-width: 340px;
    }

    .footer-text__suptitle {
        margin-bottom: 15px;
    }

    .footer-contacts-link__item {
        &:not(:last-child) {
            margin-right: 24px;
        }
    }

    .footer-contacts-link__list {
        margin-bottom: 46px;
    }

    .footer-contacts__text {
        font-size: 14px;
    }

    .copyright {
        padding-top: 30px;
    }
}

@media screen and (min-width: 1280px) {
    .footer {
        padding-bottom: 10px;

        .container {
            margin-bottom: 98px;

            display: flex;
            justify-content: space-between;
        }
    }

    .footer-text__suptitle {
        margin-bottom: 15px;
    }

    .footer-contacts-link__item {
        &:not(:last-child) {
            margin-right: 24px;
        }
    }

    .footer-contacts-link__list {
        margin-bottom: 50px;
    }

    .footer-contacts__text {
        font-size: 16px;
        letter-spacing: 0.02em;
    }

    .copyright {
        padding-top: 10px;
    }
}
